/* General Styling */
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  background-color: #121212;
  color: #ffffff;
  overflow: hidden;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  height: 100vh;
}

.app-header {
  padding: 20px;
  background: linear-gradient(45deg, #1db954, #191414);
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.6);
  width: 100%;
}

.app-header h1 {
  font-size: 2.5rem;
  color: #ffffff;
  text-shadow: 0px 0px 8px rgba(29, 185, 84, 0.8);
}

.scrollable-content {
  flex: 1;
  overflow-y: auto;
  width: 100%;
  padding: 20px;
  background-color: #191414;
  scrollbar-width: thin;
  scrollbar-color: #1db954 #333333;
}

.scrollable-content::-webkit-scrollbar {
  width: 8px;
}

.scrollable-content::-webkit-scrollbar-track {
  background: #333333;
}

.scrollable-content::-webkit-scrollbar-thumb {
  background: #1db954;
  border-radius: 8px;
}

.category-section {
  margin-bottom: 40px;
  padding: 20px;
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.8);
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.5);
}

.category-section h2 {
  font-size: 1.8rem;
  margin-bottom: 20px;
  color: #1db954;
  text-shadow: 0px 0px 6px rgba(29, 185, 84, 0.8);
}

.category-section ul {
  list-style-type: none;
  padding: 0;
}

.category-section li {
  display: flex;
  justify-content: space-between;
  font-size: 1.2rem;
  padding: 10px 20px;
  margin: 5px 0;
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.05);
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
  transition: transform 0.2s, background 0.2s;
}

.category-section li:hover {
  background: rgba(29, 185, 84, 0.1);
  transform: scale(1.05);
}

.user-name {
  font-weight: bold;
  color: #ffffff;
}

.user-count {
  font-weight: bold;
  color: #1db954;
}
